.contact-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 10px solid rgb(104, 18, 209);

  margin-top: 5%;
  text-align: center;
}

.contact-label {
  display: block;
  margin-top: 10px;
}

.contact-input {
  width: 70%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contactBtn {
  padding: 10px 20px;
  background-color: #007bff;
  font-size: 100%;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.contactBtn:hover {
  background-color: #0056b3;
}
