body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.topnav {
  overflow: hidden;
  background-color: #7c2aff;
  font-weight: 700;
  
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 10px 14px;
  text-decoration: none;
  font-size: 20px;
}

.topnav a:hover {
  background-color: #6609f1;
  color: rgb(255, 225, 0);
}

.topnav a.active {
  background-color: #04AA6D;
  color: white;
}
