/* PopupContactForm.css */

.popup-button-open {
  display: inline-block;
  outline: none;
  cursor: pointer;
  padding: 0 16px;
  background-color: #1edd05;
  border-radius: 0.25rem;
  border: 1px solid #1fe804;
  color: #fff;
  font-size: 13px;
  line-height: 30px;
  font-weight: 400;
  text-align: center;
}
.popup-button-open:hover {
  background-color: #34be15;
  border-color: #34be15;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background-color: #fff;
  padding: 30px;
  width: 50%;
  height: 60%;
  padding-right: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow: scroll;
}

.form-group-popup {
  margin-bottom: 20px;
  display: flex;
}

.popup-label {
  display: inline;
  padding-right: 2%;
  padding-left: 2%;
}

.popup-input {
  width: 25%;
  height: 100%;
  padding: 5px;
  border: 1px solid #ccc;

  border-radius: 5px;
}

.popup-button[type="submit"] {
  padding: 10px 20px;
  background-color: #007bff;
  font-weight: 500;
  font-size: 15px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup-button[type="submit"]:hover {
  background-color: #0056b3;
}

.popup-button-close[type="submit"] {
  padding: 10px 20px;
  background-color: #ff0000;
  font-weight: 500;
  font-size: 15px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup-button-close[type="submit"]:hover {
  background-color: rgb(232, 19, 19);
}
