.home {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(227, 231, 248);
}

img {
  width: 100%;
  height: 450px;
  padding-top: 5px;
}



.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}
.card {
  width: 300px;
  background-color: #464646;
  color: white;
 
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
}


.fetchBtn {
    display: inline-block;
    outline: none;
    cursor: pointer;
    padding: 0 16px;
    background-color: #0070d2;
    border-radius: 0.25rem;
    border: 1px solid #0070d2;
    color: #fff;
    font-size: 13px;
    line-height: 30px;
    font-weight: 400;
    text-align: center;
  }
  .fetchBtn:hover {
    background-color: #005fb2;
    border-color: #005fb2;
  }
  
